<template>
  <v-container
    id="regular-forms"
    fluid
    tag="section"
  >
    <v-col cols="12">
      <base-material-card
        color="primary"
        icon="mdi-map-marker"
        :title="this.$t('nav.point_of_service')"
        class="px-5 py-3"
      >
        <v-col>
          <v-card-title>
            <v-btn
              color="primary"
              @click="create"
            >
              <v-icon small>
                mdi-account-plus
              </v-icon>Create
            </v-btn>
            <v-spacer />
            <search-text-field :value.sync="search" />
          </v-card-title>
          <v-data-table
            class="pa-5"
            :loading="loading"
            :headers="headers"
            :options.sync="options"
            :server-items-length="totalItems"
            :items="point_of_service"
            :multi-sort="true"
          >
            <template v-slot:item.action="{ item }">
              <v-icon
                small
                color="warning"
                class="mr-2"
                @click="editItem(item)"
              >
                mdi-pencil
              </v-icon>
            </template>
            <template v-slot:no-results>
              <v-alert
                :value="true"
                color="error"
                icon="warning"
              >
                Your search for "{{ search }}" found no results.
              </v-alert>
            </template>
          </v-data-table>
        </v-col>
      </base-material-card>
    </v-col>
  </v-container>
</template>

<script>
  import SearchTextField from '../components/custom/SearchTextField'
  export default {
    components: { SearchTextField },
    data: () => ({
      search: '',
      headers: [
        { text: 'Locatie', value: 'id', search: true },
        { text: 'Locatie naam', value: 'name', search: true },
        { text: 'Stad', value: 'address.city', search: true },
        { text: 'Zip code', value: 'address.zip_code', search: true },
        { text: 'Actions', value: 'action', sortable: false },
      ],
      point_of_service: [],
      options: {},
      totalItems: 0,
      loading: true,
    }),

    computed: {
      params () {
        return {
          ...this.options,
          query: this.search,
          fields: this.headers,
        }
      },
    },
    watch: {
      params: {
        handler () {
          this.getDataFromApi()
        },
        deep: true,
      },
    },
    mounted () {
      this.getDataFromApi()
    },
    methods: {
      async getDataFromApi () {
        this.loading = true
        let response = await this.$http.get('/portal/point_of_services', { params: { options: this.params } })
        this.point_of_service = response.data.items
        this.totalItems = response.data.total_items_count
        this.loading = false
      },
      create () {
        this.$router.push({ name: 'point_of_service_create' })
      },
      editItem (item) {
        this.$router.push({ name: 'point_of_service_edit', params: { id: item.id } })
      },
    },
  }
</script>
